export default {
  computed: {
    releases () {
      return this.$store.state.releases
    },
    toc () {
      return this.releases.map(release => ({ id: release.name, depth: 2, text: release.name }))
    }
  },
  methods: {
    formatDate (release) {
      const date = new Date(release.date)

      return date.toLocaleDateString(this.$i18n.locale)
    }
  },
  head () {
    return {
      title: 'Releases'
    }
  }
}
